.ContactPage {
  align-content: center;
}

.ContactPage img {
  object-fit: cover;
  height: 550px;
  width: 95%;
  margin-top: 20px;
}

.ContactPage section {
  background-color: #dee;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: inline-block;
  max-width: 95%;
  padding: 40px;
  margin: 30px;
}

.ContactPage .opener {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: inline-block;
  max-width: 90%;
  padding: 40px;
  margin: 20px;
  margin-top: -400px;
}
